import bus from "@/bus"
import Vue from "vue";
export default{
    state:{
        goodsMsg:[],
        total:0,
        goodsId:''
    },
    mutations:{
        SET_GOODS_MSG(state,obj){
            state.goodsMsg = obj.goodsMsg;
            // console.log(2222222);
            for(var item in state.goodsMsg){
                state.goodsMsg[item].checkModel=true;
            }
           
            
         
        },
        CHANGE_CHECK_MODEL(state,id){
            var item = state.goodsMsg.findIndex(v=>v.id===id);
            state.goodsMsg[item].checkModel = !state.goodsMsg[item].checkModel;
            // console.log(2222,item);
            state.goodsMsg = JSON.parse(JSON.stringify(state.goodsMsg));
            var total = 0;
            for(var item in state.goodsMsg){
                // console.log(state.goodsMsg[item].checkModel)
                if(state.goodsMsg[item].checkModel)
                    total+= state.goodsMsg[item].price;
            }
            // console.log(222222,total) ;
        },

        UPDATE_GOODS_MSG(state,obj){
            state.goodsMsg = obj.goodsMsg;
        }
      
    },
    getters:{
        priceTotal(state){
            // console.log(11111111111111,this.item);
             var total = 0;
          
            for(var item in state.goodsMsg){
                if(state.goodsMsg[item].checkModel)
                    total+= state.goodsMsg[item].price;
            }
            return total;
            }
    },
    actions:{
        getShopCar({commit},obj){
            bus.$axios.post("/user/shoppingCart/list")
            .then(({data})=>{
               console.log(data);
              
                commit('SET_GOODS_MSG',{
                   goodsMsg:data.shoppingCart
                 
                   
                })
                
            })
        },
        delGoods({commit},obj){
            console.log(obj.data);
            bus.$axios.post("/user/shoppingCart/del",obj.data)
            .then(({data})=>{
                // console.log(data);
            })
        },
        updateGoods({commit},obj){
            bus.$axios.post("/pawn/products/findById/"+obj.data.ids)
            .then(({data})=>{
                console.log(data);
                commit('UPDATE_GOODS_MSG',{
                    // goodsMsg:data.data
                })
            })
        },
         SaveList({commit},obj){
            bus.$axios.post("/user/personalOrder/save",obj)
        }


    }
}