import bus from '@/bus'

export default({
    state:{
        setOrder:[],
        PhonePrefix:[],
        Country:[],
        Province:[],
        City:[],
        Area:[],
    },
    mutations:{
        SET_PHONE_PREFIX(state,obj){
            state.PhonePrefix = obj;
        },
        SET_COUNTRY(state,obj){
            state.Country = obj;
        },
        SET_PROVINCE(state,obj){
            state.Province = obj;
        },
        SET_CITY(state,obj){
            state.City = obj;
        },
        SET_AREA(state,obj){
            state.Area = obj;
        }
    },
    actions:{
        setOrder({commit},obj){
            bus.$axios.post("/user/user/addUserInfo",{
                userId:1,
                familyName: obj.familyName,
                name: obj.trueName,
                nationality : obj.nationality,
                phone : obj.phone,
                email : obj.email,
                sex : obj.sexe,
                birthday : obj.birthday,
                country: obj.internationalAreaCode[3] + obj.internationalAreaCode[4],
                //获取国家区号。逻辑为： +xxx，如果数字为3位数，也就是如果有第四个数，
                //就输出 数组的第6，7，8，9，四个值。如果没有，就输出6，7，8三个值。
                prefix: obj.internationalAreaCode[9]? obj.internationalAreaCode[6]+
                        obj.internationalAreaCode[7]+ obj.internationalAreaCode[8]+
                        obj.internationalAreaCode[9]:
                        obj.internationalAreaCode[6]+
                        obj.internationalAreaCode[7]+ obj.internationalAreaCode[8],
                province: obj.province,
                city: obj.city,
                region: obj.area,
                status: obj.addr,
                detailed: obj.address1

            })
                .then(({data})=>{
                    console.log(data);
                    console.log(obj);
                })
        },
        Country({commit}){
            bus.$axios.post("/user/country/list")
                .then(({data})=>{
                    // console.log('进入Country')
                    this.state.Country = data.countryList;
                    // console.log("Country:----------------" );
                    // console.log(data.countryList);
                    commit("SET_COUNTRY",data.countryList)
                })
        },
        PhonePrefix({commit}){
            bus.$axios.post("/pawn/phonePrefix/list")
                .then(({data})=>{
                    // console.log('进入getProvinceList')
                    this.state.PhonePrefix = data.phonePrefix;
                    // console.log("PhonePrefix:----------------" );
                    // console.log(data.phonePrefix);
                    commit("SET_PHONE_PREFIX",data.phonePrefix)
                })
        },
        Province({commit}){
            bus.$axios.post("/user/address/provinceList")
                .then(({data})=>{
                    // console.log('进入getProvinceList')
                    this.state.Province = data.province;
                    // console.log(data.province);
                    // console.log(data);
                    commit("SET_PROVINCE",data.province)
                })
        },
        City({commit},province){
            // console.log(11111111111111111);
            bus.$axios.post("/user/address/city/" + province)
                .then(({data})=>{
                    console.log('进入city-----------');
                    this.state.City = data;
                    // console.log(data);
                    // console.log(province);
                    commit("SET_CITY",data.city);
                })
        },
        Area({commit},city){
            // console.log(11111111111111111);
            bus.$axios.post("/user/address/area/" + city)
                .then(({data})=>{
                    // console.log(11111111111111111);
                    this.state.Area = data.area;
                    // console.log(data.area);
                    commit("SET_AREA",data.area);
                })
        }

    }
})