import bus from '@/bus'

export default({
    state:{
        time: '',
        oderNum: '',
        inflow: '',
        balance:'',
        moneyTo:''
    },
    mutations:{
        SET_MUTATION_TABLE(state,obj){
            state.time = obj[0].time;
            state.oderNum = obj[0].orderNumber;
            state.balance = obj[0].balance;
            state.moneyTo = obj[0].status
        },
    },
    actions:{
        getActionTable({commit},obj){
            bus.$axios.post('/user/user/assetDetail')
            .then(({data})=>{
                //console.log(data)
                commit("SET_MUTATION_TABLE",data.assetDetailList)
            })
        }
    }
})